import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Router, ActivatedRoute } from '@angular/router';
import { AuthService, FacebookLoginProvider, SocialUser } from 'angularx-social-login';
import { FacebookService } from '../../services/facebook/facebook.service'
const REGEX_EMAIL = new RegExp(['^(([^<>()\\[\\]\\\.,;:\\s@"]+(\\.[^<>()\\[\\]\\\.,;:\\s@"]+)*)|(".+"))',
  '@((\\[[0-9]{1,3}\\.[0-9]{1,3}\\.[0-9]{1,3}\\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\\.)+[a-zA-Z]{2,}))$'].join(''));

@Component({
	selector: 'sq-login',
	templateUrl: './login.component.html',
	styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit {
	User: SocialUser;
	loggedIn: boolean;
	pages: [any];
	public user = {
		email: '',
		password: ''
	};
	public btnDisabled = false;
	public showErrorLogin = false;
	public registerSuccess = false;
	public formGroup: FormGroup;
	public password = '';
	public typePassword = 'password';
	public iconPassword = 'zmdi-eye';
	public showWarningEmail = false;
	// Viewchilds
	@ViewChild('password')
	private passwordElement: ElementRef;

	constructor(
		private facebookService: FacebookService,
		private _router: Router,
		private _activatedRoute: ActivatedRoute,
		private fb: FormBuilder,
		private authService: AuthService
	) {
		this.createForm();
		this._activatedRoute.queryParams.subscribe((params) => {
			const registerSuccess = params['registerSuccess'];
			if (registerSuccess) {
				this.registerSuccess = true;
			}
		});
	}
	signInWithFB(): void {
		this.authService.signIn(FacebookLoginProvider.PROVIDER_ID);
	  }
	  
	  signOut(): void {
		this.authService.signOut();
	  }

	ngOnInit() {
		this.authService.authState.subscribe((user) => {
			this.User = user;
			this.loggedIn = (user != null);
			console.log(this.User?this.User.authToken : null);
			this.facebookService.getPageService(this.User?this.User.authToken : null).then(e=>{
				this.pages=e
				console.log(this.pages);
				
			})
			console.log(this.User);
		  });
	}

	createForm() {
		this.formGroup = this.fb.group({
			email: ['', [Validators.required,
			Validators.pattern(REGEX_EMAIL)]],
			password: ['', [Validators.required]]
		});
		this.formGroup.valueChanges.subscribe(val => {
			this.showErrorLogin = false;
		});
	}

	showPassword() {
		if (this.formGroup.controls.password.value.length > 0) {
			if (this.typePassword === 'password') {
				this.typePassword = 'text';
				this.iconPassword = 'zmdi-eye-off';
			} else {
				this.typePassword = 'password';
				this.iconPassword = 'zmdi-eye';
			}
		}
	}

	focusNextInput() {
		this.showWarningEmail = true;
		this.passwordElement.nativeElement.focus();
	}

	login() {
		this.showErrorLogin = false;
		this.btnDisabled = true;
		console.log('login');
		this._router.navigate(['dashboard']);
	}

}
