import { Injectable } from '@angular/core';

@Injectable({
	providedIn: 'root'
})
export class FacebookService {
  
    async getPageService(userToken){
        //Eliminar despues
        userToken= `EAAKoAQhf3dUBADKrwWJ3HnqzbnI70uCqXS3ZCLTsUhu0FjbOAjGcZAX8eBzf0zFwSwy3B2QCivE5zWPXfPqqowtDaHgbKaVgBGY6rZAcGtGe9ZAhKKuf00LLqTvTQGNLkszHNfhxvZAO2bNopFZBhkwtVH3ZBXfZBxGHrg6N0U2zdKuTzDcJI33ZA6KDfLdMNzmQZD`

        let respuesta: any ={}
        await fetch(`https://graph.facebook.com/v5.0/me/accounts/?access_token=${userToken}&fields=access_token,id,name,cover,picture.type(large),username,instagram_business_account,displayed_message_response_time,engagement,unread_message_count,unseen_message_count,fan_count&limit=100&summary=total_count`)
        .then(res => res.json())
        .then(res=> respuesta = res)
        return respuesta.data.map(e=>{
            return{
                name : e.name,
                pageToken : e.access_token,
                image: e.picture.data.url,
                nick : e.username
            }
        })
    }
	constructor() { }

}
