import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { query } from '@angular/core/src/render3';

const REGEX_EMAIL = new RegExp(['^(([^<>()\\[\\]\\\.,;:\\s@"]+(\\.[^<>()\\[\\]\\\.,;:\\s@"]+)*)|(".+"))',
	'@((\\[[0-9]{1,3}\\.[0-9]{1,3}\\.[0-9]{1,3}\\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\\.)+[a-zA-Z]{2,}))$'].join(''));
// const REGEX_EMAIL = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>
// ()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;

@Component({
	selector: 'sq-register-form',
	templateUrl: './register-form.component.html',
	styleUrls: ['./register-form.component.scss']
})
export class RegisterFormComponent implements OnInit {
	public user = {
		email: '',
		password: ''
	};
	public btnDisabled = false;
	public showErrorRegister = false;
	public formGroup: FormGroup;
	public password = '';
	public typePassword = 'password';
	public iconPassword = 'zmdi-eye';
	public showWarningEmail = false;
	public countPassword = false;
	public showWarningPassword = false;
	public showSuccess = false;

	constructor(
		private fb: FormBuilder,
		private _router: Router
	) {
		this.createForm();
	}

	ngOnInit() {
	}

	createForm() {
		this.formGroup = this.fb.group({
			email: ['', [Validators.required,
			Validators.pattern(REGEX_EMAIL)]],
			password: ['', [Validators.required, Validators.pattern(/^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)[a-zA-Z\d]{8,}$/)]]
		});
		this.formGroup.valueChanges.subscribe(val => {
			this.showErrorRegister = false;
		});
	}

	getCharactersNumber() {
		const aux = 8 - this.formGroup.controls.password.value.length;
		if (aux < 0) {
			return 0;
		} else {
			return aux;
		}
	}

	focusPassword() {
		this.countPassword = true;
		this.showWarningPassword = false;
	}


	blurPassword() {
		this.countPassword = false;
		this.showWarningPassword = true;
	}

	showPassword() {
		if (this.formGroup.controls.password.value.length > 0) {
			if (this.typePassword === 'password') {
				this.typePassword = 'text';
				this.iconPassword = 'zmdi-eye-off';
			} else {
				this.typePassword = 'password';
				this.iconPassword = 'zmdi-eye';
			}
		}
	}

	register() {
		this.btnDisabled = true;
		this.showErrorRegister = false;
		// localStorage.setItem('user', this.user.email);
		this.showSuccess = true;
		// this._router.navigate(['register/validation']);
	}

}
