import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';

const REGEX_EMAIL = new RegExp(['^(([^<>()\\[\\]\\\.,;:\\s@"]+(\\.[^<>()\\[\\]\\\.,;:\\s@"]+)*)|(".+"))',
	'@((\\[[0-9]{1,3}\\.[0-9]{1,3}\\.[0-9]{1,3}\\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\\.)+[a-zA-Z]{2,}))$'].join(''));

@Component({
	selector: 'sq-recovery-form',
	templateUrl: './recovery-form.component.html',
	styleUrls: ['./recovery-form.component.scss']
})
export class RecoveryFormComponent implements OnInit {

	public showSuccess = false;
	public showError = false;
	public formGroup: FormGroup;
	public showWarningEmail = false;


	constructor(
		private fb: FormBuilder
	) {
		this.createForm();
	}

	ngOnInit() {
	}

	createForm() {
		this.formGroup = this.fb.group({
			email: ['', [Validators.required,
			Validators.pattern(REGEX_EMAIL)]]
		});
	}

	sendEmail() {
		this.showSuccess = true;
	}

}
