import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
/** CORE */
import { CoreModule } from './../core/core.module';
/** MODULES */
import { SharedModule } from './../shared/shared.module';
/** ROUTINGS */
import { RouterModule } from '@angular/router';
import { SwiperModule, SwiperConfigInterface, SWIPER_CONFIG } from 'ngx-swiper-wrapper';
import { ComponentsPublicComponent } from './components-public.component';
import { ComponentsPublicRouting } from './components-public.routing';
import { NgbTabsetModule } from '@ng-bootstrap/ng-bootstrap';
import { NgxPaginationModule } from 'ngx-pagination';
import { LoginComponent } from './login/login.component';
import { ReactiveFormsModule, FormsModule } from '@angular/forms';
import { RegisterModule } from './register/register.module';
import { RecoveryModule } from './recovery/recovery.module';
import { MatInputModule } from '@angular/material/input';
import { MatFormFieldModule } from '@angular/material/form-field';
import { MatProgressSpinnerModule } from '@angular/material/progress-spinner';
import { MatOptionModule } from '@angular/material/core';

const DEFAULT_SWIPER_CONFIG: SwiperConfigInterface = {
	direction: 'horizontal',
	slidesPerView: 1,
	spaceBetween: 15
};
/**
 * Módulo principal de conponentes
 * @export
 * @class ComponentsModule
 */
@NgModule({
	imports: [
		CommonModule,
		RouterModule,
		ComponentsPublicRouting,
		SharedModule,
		CoreModule,
		SwiperModule,
		// NgbTabsetModule,
		// NgxPaginationModule,
		ReactiveFormsModule,
		RegisterModule,
		RecoveryModule,
		FormsModule,
		MatFormFieldModule,
		MatInputModule
		// MatOptionModule,
		// MatProgressSpinnerModule
	],
	declarations: [
		//Component
		ComponentsPublicComponent,
		LoginComponent
	],
	exports: [
		ComponentsPublicComponent,
		LoginComponent
	],
	providers: [
		{
			provide: SWIPER_CONFIG,
			useValue: DEFAULT_SWIPER_CONFIG
		}
	]

})
export class ComponentsPublicModule { }
