import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';

@Component({
	selector: 'sq-recovery-form-new-pass',
	templateUrl: './recovery-form-new-pass.component.html',
	styleUrls: ['./recovery-form-new-pass.component.scss']
})
export class RecoveryFormNewPassComponent implements OnInit {

	public formGroup: FormGroup;
	public password = '';
	public typePassword = 'password';
	public iconPassword = 'zmdi-eye';
	public confirmPassword = '';
	public typeConfirmPassword = 'password';
	public iconConfirmPassword = 'zmdi-eye';
	public showSuccess = false;
	public showError = false;
	public countNewPassword = false;
	public showWarningNewPassword = false;
	public showWarningConfirmPassword = false;

	@ViewChild('confirmPassword')
	private confirmPasswordElement: ElementRef;

	constructor(
		private fb: FormBuilder
	) {
		this.createForm();
	}

	ngOnInit() {
	}

	createForm() {
		this.formGroup = this.fb.group({
			password: ['', [Validators.required, Validators.pattern(/^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)[a-zA-Z\d]{8,}$/)]],
			confirmPassword: ['', [Validators.required, Validators.pattern(/^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)[a-zA-Z\d]{8,}$/)]]
		});
	}

	getCharactersNumber() {
		const aux = 8 - this.formGroup.controls.password.value.length;
		if (aux < 0) {
			return 0;
		} else {
			return aux;
		}
	}

	focusNewPassword() {
		this.countNewPassword = true;
		this.showWarningNewPassword = false;
	}


	blurNewPassword() {
		this.countNewPassword = false;
		this.showWarningNewPassword = true;
		// this.confirmPasswordElement.nativeElement.focus();

	}

	showPassword() {
		if (this.formGroup.controls.password.value.length > 0) {
			if (this.typePassword === 'password') {
				this.typePassword = 'text';
				this.iconPassword = 'zmdi-eye-off';
			} else {
				this.typePassword = 'password';
				this.iconPassword = 'zmdi-eye';
			}
		}
	}

	showConfirmPassword() {
		if (this.formGroup.controls.confirmPassword.value.length > 0) {
			if (this.typeConfirmPassword === 'password') {
				this.typeConfirmPassword = 'text';
				this.iconConfirmPassword = 'zmdi-eye-off';
			} else {
				this.typeConfirmPassword = 'password';
				this.iconConfirmPassword = 'zmdi-eye';
			}
		}
	}

	savePassword() {
		this.showSuccess = true;
	}

}
