import { Routes, RouterModule } from '@angular/router';
import { NgModule } from '@angular/core';
/** GUARDS */
import { GuardsModule } from './../guards/guards.module';
import { ComponentsPublicComponent } from './components-public.component';
import { LoginComponent } from './login/login.component';
import { RegisterComponent } from './register/register.component';
import { RegisterFormComponent } from './register/register-form/register-form.component';
import { RegisterValidationComponent } from './register/register-validation/register-validation.component';
import { RecoveryComponent } from './recovery/recovery.component';
import { RecoveryFormComponent } from './recovery/recovery-form/recovery-form.component';
import { RecoveryFormNewPassComponent } from './recovery/recovery-form-new-pass/recovery-form-new-pass.component';
/** COMPONENTS */

const routes: Routes = [
	{
		path: '', component: ComponentsPublicComponent, children: [
			{ path: 'login', component: LoginComponent },
			{
				path: 'register', component: RegisterComponent, children: [
					{ path: '', component: RegisterFormComponent },
					{ path: 'validation', component: RegisterValidationComponent }
				]
			},
			{
				path: 'recovery', component: RecoveryComponent, children: [
					{ path: '', component: RecoveryFormComponent },
					{ path: 'new-password', component: RecoveryFormNewPassComponent }
				]
			},
			{ path: '', redirectTo: 'login', pathMatch: 'full' }

		]
	}

];

/**
 * CONTENEDOR PRINCIPAL DE RUTAS EN COMPONENTS
 * @export
 * @class ComponentsRouting
 */
@NgModule({
	imports: [GuardsModule, RouterModule.forChild(routes)],
	exports: [RouterModule]
})
export class ComponentsPublicRouting { }
